import React, { Component } from "react";
import axios from "axios";
import Flex from "components/shared-components/Flex";
import moment from "moment";
import { RecentTransactionData } from "views/app-views/dashboards/default/DefaultDashboardData";
import { Tag, Card, Table, Dropdown, Row, Col, Avatar } from "antd";
import { SPACER } from "constants/ThemeConstant";
import {
	FileExcelOutlined,
	PrinterOutlined,
	ReloadOutlined,
	EllipsisOutlined,
} from "@ant-design/icons";
import utils from "utils";

const transactionOption = [
	{
		key: "Refresh",
		label: (
			<Flex alignItems="center" gap={SPACER[2]}>
				<ReloadOutlined />
				<span className="ml-2">Refresh</span>
			</Flex>
		),
	},
	{
		key: "Print",
		label: (
			<Flex alignItems="center" gap={SPACER[2]}>
				<PrinterOutlined />
				<span className="ml-2">Print</span>
			</Flex>
		),
	},
	{
		key: "Export",
		label: (
			<Flex alignItems="center" gap={SPACER[2]}>
				<FileExcelOutlined />
				<span className="ml-2">Export</span>
			</Flex>
		),
	},
];

const CardDropdown = ({ items }) => {
	return (
		<Dropdown menu={{ items }} trigger={["click"]} placement="bottomRight">
			<a
				href="/#"
				className="text-gray font-size-lg"
				onClick={(e) => e.preventDefault()}
			>
				<EllipsisOutlined />
			</a>
		</Dropdown>
	);
};

export class TransactionsComponent extends Component {
	state = {
		transactions: [],
	};

	componentDidMount() {
		axios.get("http://localhost:5000/api/management/allTrxs").then((res) => {
			this.setState({
				transactions: res.data.allTransactions,
			});
		});
	}

	render() {
		const { transactions } = this.state;
		const recentTransactionData = RecentTransactionData.filter(
			(item) => item.status === "Pending"
		);
		const defaultAvatar = "/img/avatars/default.png";
		const tableColumns = [
			{
				title: "Customer",
				dataIndex: "name",
				key: "name",
				render: (text, record) => (
					<div className="d-flex align-items-center">
						<Avatar
							size={30}
							className="font-size-sm"
							src={record.image}
							style={{ backgroundColor: record.avatarColor }}
						>
							{utils.getNameInitial(text)}
						</Avatar>
						<span className="ml-2">{text}</span>
					</div>
				),
			},
			{
				title: "Date",
				dataIndex: "date",
				key: "date",
				render: (text, record) => (
					<span>{moment.unix(text).format("D MMM YYYY, HH:mm")}</span>
				),
			},
			{
				title: "Amount $",
				dataIndex: "amount",
				key: "amount",
			},
			{
				title: <div className="text-center">Method</div>,
				dataIndex: "paymentType",
				key: "paymentType",
				render: (_, elm) => (
					<div className="text-center">
						<Avatar
							src={
								elm.paymentType !== ""
									? "/img/others/" + elm.paymentType + ".png"
									: defaultAvatar
							}
							alt={elm.paymentType}
							preview={false}
							style={{ height: "25px", width: "auto" }}
						/>
					</div>
				),
			},
			{
				title: () => <div className="text-right">Status</div>,
				key: "status",
				render: (_, record) => (
					<div className="text-right">
						<Tag
							className="mr-0"
							color={
								record.paymentStatus === "Pending"
									? "cyan"
									: record.paymentStatus === "Paid"
									? "blue"
									: "volcano"
							}
						>
							{record.paymentStatus}
						</Tag>
					</div>
				),
			},
			// {
			// 	title: () => <div className="text-right">Action</div>,
			// 	dataIndex: "action",
			// 	key: "action",
			// 	render: (_, elm) => (
			// 		<div className="text-right d-flex justify-content-end">
			// 			<Tooltip title="View">
			// 				<Link to={`/app/pages/user-detail/${elm.userId}`}>
			// 					<Button
			// 						type="primary"
			// 						className="mr-2"
			// 						icon={<EyeOutlined />}
			// 						size="small"
			// 					/>
			// 				</Link>
			// 			</Tooltip>
			// 		</div>
			// 	),
			// },
		];

		return (
			<>
				<Row>
					<Col xs={24} sm={24} md={24} lg={24}>
						<Card
							title="All Transactions"
							extra={<CardDropdown items={transactionOption} />}
						>
							<Table
								className="no-border-last"
								columns={tableColumns}
								dataSource={transactions}
								rowKey="id"
								pagination={false}
							/>
						</Card>
					</Col>
				</Row>
			</>
		);
	}
}

export default TransactionsComponent;
